.header__logo {
  max-width: 142px;
  width: 100%;
  padding-top: 0px;
  padding-left: 0px;
}

@media screen and (max-width: 934px) {
  .header__logo {
    padding-left: 30px;
    padding-bottom: 0;
  }
}

@media screen and (max-width: 320px) {
  .header__logo {
    max-width: 103.74px;
    padding-top: 0px;
  }
}
